.button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-tap-highlight-color: transparent;

  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--ui-color-text-primary);
  transition: all .2s;

  &:disabled {
    cursor: default;
  }

  &:focus-visible {
    transition: none;
    outline: var(--ui-border-width-medium) solid var(--ui-color-primary);
    outline-offset: 2px;

    &:not(.button_shape_circle) {
      border-radius: var(--ui-element-border-radius-medium);
    }
  }

  &_size {
    &_small {
      padding: 4px 6px;
      height: var(--ui-element-size-small);
      border-radius: var(--ui-element-border-radius-small);
    }

    &_medium {
      padding: 8px 16px;
      height: var(--ui-element-size-medium);
      border-radius: var(--ui-element-border-radius-medium);
    }

    &_large {
      font-size: var(--ui-font-size-lg);
      line-height: var(--ui-line-height-lg);
      padding: 12px 16px;
      height: var(--ui-element-size-large);
      border-radius: var(--ui-element-border-radius-large);
    }
  }

  &_shape {
    &_circle,
    &_square {
      &.button_size_small {
        width: var(--ui-element-size-small);
      }

      &.button_size_medium {
        width: var(--ui-element-size-medium);
      }

      &.button_size_large {
        width: var(--ui-element-size-large);
      }
    }

    &_circle {
      border-radius: 50%;
    }
  }

  &_type {
    &_primary {
      color: var(--ui-color-primary-inverted);
      background-color: var(--ui-color-primary);

      &:not(:disabled):hover {
        background-color: var(--ui-color-primary-lightened);
      }

      &:not(:disabled):active {
        background-color: var(--ui-color-primary-darkened);
      }
    }

    &_secondary {
      background-color: #fff;
      border: var(--ui-border-width-small) solid var(--ui-color-border-primary);

      &:not(:disabled):hover {
        color: var(--ui-color-primary-lightened);
        border-color: var(--ui-color-primary-lightened);
      }

      &:not(:disabled):active {
        color: var(--ui-color-primary-darkened);
        border-color: var(--ui-color-primary-darkened);
      }
    }

    &_text {
      &:not(:disabled):hover {
        background-color: var(--ui-color-button-text-hover);
      }

      &:not(:disabled):active {
        background-color: var(--ui-color-button-text-active);
      }
    }
  }

  &_type_primary:disabled, &_type_secondary:disabled {
    cursor: not-allowed;
    border: var(--ui-border-width-small) solid var(--ui-color-border-primary);
    color: var(--ui-color-text-disabled);
    background-color: var(--ui-color-background-disabled);
  }

  &_type_text:disabled {
    cursor: not-allowed;
    color: var(--ui-color-text-disabled);
  }
}